@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
html{margin:0;padding:0;font-family:"Barlow", sans-serif;background:linear-gradient(to left, #1c1c1c, #282828)}

.author{padding-bottom:5px;display:flex;flex-direction:row;justify-content:space-between;font-size:18px}

.comment{margin:10px auto;padding:10px;width:400px;background:linear-gradient(#76bfa5, #5a9991);border-radius:20px;transition:0.8s}.comment:hover{margin-bottom:20px;margin-top:20px;background:linear-gradient(#91eacb, #76c8bd)}.comment__name{margin-bottom:10px;font-size:20px}.comment__body{margin-bottom:10px;font-style:italic}.comment__email{margin-bottom:5px;text-align:right;text-decoration:underline}

.comments__title{font-size:22px;font-weight:bold}

.post{margin:0 auto 40px;padding:20px;width:900px;background-color:#bce5d7;border-radius:150px;transition:0.5s}.post:hover{background-color:#ace5d5;box-shadow:1px 1px #53ea93, 2px 2px #53ea93, 3px 3px #53ea93, 4px 4px #53ea93, 5px 5px #53ea93, 6px 6px #53ea93, 7px 7px #53ea93;-webkit-transform:translateX(-7px);transform:translateX(-7px)}.post__title{margin-bottom:20px;text-transform:uppercase;text-align:center;font-size:40px;text-shadow:1px 1px white, 2px 2px #777;color:#333}.post__text{margin-bottom:20px;text-align:center;font-size:30px}.post__user{margin-bottom:20px}

